<template>
    <div style="background-color: #f4f5f5">
        <RawMaterialName :item="{}" :rawmaterialname="traceTitle"></RawMaterialName>
        <div class="mc-wrap mc-info flex-sb">
            <h3 style="font-weight: bold; flex-shrink: 0; width: 80px; margin-right: 15px">工业标识码</h3>
            <div class="currency-price">
                <span style="font-weight: bold; word-break: break-all">{{ idisCode }}</span>
            </div>
        </div>
        <div>
            <div class="detail-card" v-for="(category, i) in traceDataList" :key="i">
                <h3 class="detail-title">{{ category.categoryName }}</h3>
                <div class="mc-info mc-wrap" v-if="category.values.length">
                    <div class="trace-detail" v-for="attr in category.values" :key="attr.id">
                        <div class="trace-detail-key">
                            {{ attr.attrNameCn }}
                        </div>
                        <span class="pull-right">{{ attr.attrValue }}</span>
                    </div>
                </div>
                <template-idis-reference
                    v-for="attr in category.references"
                    :key="attr.id"
                    :attr="attr"
                    style="font-size: 14px;"
                >
                </template-idis-reference>
            </div>
        </div>
    </div>
</template>

<script>
import { getJsonResult } from '@/utils/server-wrapper'
import Badge from '@/assets/images/code-information-badge-3.png'
import RawMaterialName from '@/components/product-template/RawMaterialName'
import TemplateIdisReference from '@/components/product-template/TemplateIdisReference'

export default {
    components: {
        RawMaterialName,
        TemplateIdisReference
    },
    data() {
        return {
            Badge,
            traceTitle: '',
            idisCode: '',
            traceDataList: []
        }
    },
    async beforeRouteEnter(to, from, next) {
        const idisCode = to.query.code
        const materialName = to.query.materialName
        const info = await getJsonResult(`/wx/codeInformation?codeId=${encodeURIComponent(idisCode)}`)
        const codeAttr = info.codeAttr

        // 将属性列表整理为属性分类的列表
        const attrList = []
        for (let item of codeAttr) {
            const catName = item.attrCategoryName
            if (catName && catName.startsWith(materialName + '/')) {
                attrList.push(item)
            }
        }
        attrList.sort((a, b) => {
            if (a.attrCategoryName === b.attrCategoryName) {
                return a.attrIndex - b.attrIndex
            } else if (!a.attrCategoryName || !b.attrCategoryName) {
                return a.categoryName ? 1 : -1
            } else {
                return a.attrCategorySort - b.attrCategorySort
            }
        })
        let traceDataList = []
        for (let item of attrList) {
            let catName = item.attrCategoryName
            catName = catName.substring(catName.indexOf('/') + 1)
            let category = traceDataList[traceDataList.length - 1] || {}
            // console.log(catName, category.categoryName, category.categoryName !== catName)
            if (category.categoryName !== catName) {
                // 属性分类的结构
                category = {
                    categoryName: catName,
                    values: [],
                    references: []
                }
                traceDataList.push(category)
            }
            if (item.references && item.references.length > 0) {
                category.references.push(item)
            } else if (item.attrValue && item.attrValue.startsWith('JSON--{')) {
                try {
                    const json = JSON.parse(item.attrValue.substring(6))
                    for (let key of Object.keys(json)) {
                        category.values.push({
                            attrNameCn: key,
                            attrValue: json[key]
                        })
                    }
                    continue
                } catch {
                    // do nothing
                }
                category.values.push(item)
            } else if (item.attrValue) {
                category.values.push(item)
            }
        }
        traceDataList = traceDataList.filter(i => i.values.length + i.references.length > 0)
        console.log('tracedatalist', traceDataList)
        next(vm => {
            vm.$data.idisCode = idisCode
            vm.$data.traceTitle = materialName + '溯源信息'
            vm.$data.traceDataList = traceDataList
        })
    }
}
</script>

<style>
