    background-color: #ffffff;
    padding: 15px;
    margin-top: 5px;
    margin-bottom: 5px;
}

.mc-info {
    background-color: #fff;
    text-align: left;
}

.detail-card {
    width: 95%;
    margin: 10px auto;
}

.detail-title {
    text-align: left;
    font-size: 16px;
    font-weight: 700;
    margin-top: 10px;
    margin-bottom: 10px;
}

.trace-detail {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 8px 5px 5px 5px;
    text-align: left;
    width: 100%;
    font-size: 14px;
}

.trace-detail:not(:last-child) {
    padding-bottom: 10px;
    margin-bottom: 5px;
    border-bottom: 1px solid #e6e6e6;
}

.trace-detail-key {
    color: #666666;
    word-break: break-all;
}
</style>
